@use "global" as *;

.p-top-interview {
  display: flex;
  position: relative;
  --scaleX: 0;
  transform-origin: top;
  overflow-x: clip;
  background: #fff;
}
.p-top-interview::before {
  position: absolute;
  content: "";
  background: $black;
  width: 100%;
  height: 1px;
  top: 0;
  left: 0;
  z-index: 10;
  transform: scaleX(var(--scaleX));
  transform-origin: top left;
}
.p-top-interview__content {
  padding-top: rem(100);
  padding-bottom: rem(100);
}

.p-top-interview__title .c-section-title {
  display: flex;
  align-items: center;
  gap: rem(40);
}

.p-top-interview__row {
  margin-top: rem(28);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.p-top-interview__gradation-text {
  color: #fff;
  font-size: rem(26);
  font-weight: 700;
  line-height: 1.4;
  letter-spacing: 0.06em;
}

.p-top-interview__slider {
  margin-top: rem(48);
  /* max-width: rem(1075);
  margin-left: auto; */
  position: relative;
}

.swiper {
}

.swiper1 {
}

.swiper-wrapper {
}

.swiper-slide {
}

.p-top-interview__slider-card {
  padding-top: rem(15);
  padding-left: rem(15);
  padding-right: rem(45);
  padding-bottom: rem(41);
  background: #f2f2f2;
  display: block;
}

.p-top-interview__slider-img {
  padding-left: rem(30);
  margin-top: rem(-40);
}
.p-top-interview__slider-img img {
  aspect-ratio: 260 / 260;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.p-top-interview__slider-detail {
  padding-top: rem(5);
  padding-left: rem(30);
}

.p-top-interview__slider-name {
  font-size: rem(24);
  font-weight: 700;
  line-height: 1.4;
  letter-spacing: 0.04em;
  color: $black;
  font-family: $second-font-family;
  text-transform: uppercase;
}

.p-top-interview__slider-text {
  margin-top: rem(25);
  font-size: rem(17);
  line-height: 1.5;
  letter-spacing: 0.04em;
  color: $black;
  font-weight: 700;
}

.p-top-interview__tag-wrapper {
  margin-top: rem(25);
  display: flex;
  flex-wrap: wrap;
  column-gap: rem(15);
  row-gap: rem(12);
}

.p-top-interview__tag {
  font-size: rem(12);
  font-weight: 500;
  color: $black;
  letter-spacing: 0.04em;
  line-height: 1;
}
.p-top-interview__prev {
  position: absolute;
  left: rem(-27);
  top: 50%;
  transform: translateY(-50%);
}
.p-top-interview__next {
  position: absolute;
  right: rem(-27);
  top: 50%;
  transform: translateY(-50%);
}
.p-top-interview__nav-img {
  display: inline-block;
  width: rem(54);
}
.p-top-interview__nav-img img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}
.p-top-interview__num-wrapper {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.p-top-interview__no {
  font-size: rem(16);
  font-weight: 500;
  color: $black;
  letter-spacing: 0.14em;
  font-family: $second-font-family;
  writing-mode: vertical-lr;
  text-orientation: mixed;
  font-weight: 500;
  text-transform: uppercase;
  padding-left: rem(8);
}

.p-top-interview__num {
  font-size: rem(30);
  font-weight: 500;
  color: $black;
  letter-spacing: 0.14em;
  font-family: $second-font-family;
}
