@use "global" as *;

.c-left {
  background: #fff;
  min-width: rem(75);
  max-width: rem(75);
  height: auto;
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  position: relative;
  --scaleY: 0;
  transform-origin: top;
 /*  transition: transform 0.3s ease-in-out; */
}
.c-left::before {
  position: absolute;
  content: "";
  background: #413c37;
  width: 1px;
  height: 100%;
  top: 0;
  right: 0;
  transform: scaleY(var(--scaleY));
  transform-origin: top left;
}

.c-left__text {
  writing-mode: vertical-rl;
  text-transform: uppercase;
  font-family: $second-font-family;
  font-size: rem(20);
  font-weight: 600;
  letter-spacing: 0.04em;
  padding-top: rem(100);
}
