/* スライダー全体 */
.slider-wrapper {
  display: flex; /* スライドのグループを横並び */
  overflow: hidden; /* はみ出たスライドを隠す */
  gap: 1.25rem;
}
@media screen and (min-width: 768px) {
  .slider-wrapper {
    gap: min(1.875rem, 1.5625vw);
    margin-right: calc(50% - 50vw);
    margin-left: calc(50% - 50vw);
  }
}

.slider-wrapper + .slider-wrapper {
  margin-top: 2.1875rem;
}

/* スライド3枚のグループ */
.slider {
  animation: scroll-left 40s infinite linear 0.5s both;
  display: flex; /* スライド3枚を横並び */
  gap: 1.25rem;
}
@media screen and (min-width: 768px) {
  .slider {
    gap: min(1.875rem, 1.5625vw);
  }
}

.slider--right {
  animation: scroll-right 40s infinite linear 0.5s both;
}

/* スライド */
.slide {
  width: 50vw;
}
@media screen and (min-width: 768px) {
  .slide {
    width: 10vw; /* 3はスライドの枚数 */
  }
}

/* スライドの画像 */
.slide img {
  display: block;
  width: 100%;
}

/* CSSアニメーション */
@keyframes scroll-left {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
@keyframes scroll-right {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
.p-slider__img img {
  aspect-ratio: 400/400;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.section__marquee {
  display: flex;
  white-space: nowrap;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  width: 100vw;
}

.marquee__text {
  display: inline-block;
  padding-right: 0.5em;
  animation: marquee 10s linear infinite;
  color: #fff;
  font-family: "Outfit", sans-serif;
  font-size: 5.5rem;
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.marquee__text--transparent {
  color: transparent;
  -webkit-text-stroke: 1px #fff; /* ベンダープレフィックス */
  text-stroke: 1px #fff;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
